@font-face {
  font-family: TekoBold;
  font-display: swap;
  src: url(../../Fonts/Teko-Bold.ttf);
}
@font-face {
  font-family: TekoRegular;
  src: url(../../Fonts/Teko-Medium.ttf);
  font-display: swap;
}
@font-face {
  font-family: Archivo;
  src: url(../../Fonts/ArchivoBlack-Regular.ttf);
  font-display: swap;
}
@font-face {
  font-family: Nunito;
  src: url(../../Fonts/Nunito-VariableFont_wght.ttf);
  font-display: swap;
}
/*Contact Form*/
.ContactForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.ContactForm h3 {
  color: #fff;
  font-weight: bold;
  font-family: TekoBold, sans-serif;
  letter-spacing: 5px;
  text-transform: uppercase;
  font-size: 2rem;
  margin-top: 3vh;
  margin-bottom: 0px;

  background: linear-gradient(
    to right,
    hsl(0, 0%, 30%) 20%,
    hsl(0, 0%, 100%) 50%,
    hsl(0, 0%, 30%) 100%
  );
  -webkit-background-clip: text;
  color: transparent;
  animation: shine 5s infinite linear;
}
.ContactForm h4 {
  font-size: 1.2rem;
  color: #fff;
  font-family: Nunito;
  margin-bottom: 4vh;
  text-align: center;
}
.form1 {
  display: flex;
  gap: 50px;
}
.form1 div {
  display: flex;
  flex-direction: column;
}
form {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
input,
textarea {
  color: #ffffff;
  border: 0;
  padding: 0.6em;
  padding-left: 0;
  background: transparent;
  border-bottom: 1px solid white;
}
label {
  color: white;
  font-family: Nunito, sans-serif;
  font-size: 1rem;
  font-weight: bold;
}
.error {
  font-family: Nunito, sans-serif;
  font-size: 1rem;
  color: #d8000c;
  background-color: #ffbaba;
  display: flex;
  gap: 10px;
  padding: 2%;
}
.errorPic {
  width: 20px;
}

.success {
  font-family: Nunito, sans-serif;
  font-size: 2rem;
  color: #dff2bf;
}
.success:hover {
  border-bottom: 2px solid #dff2bf;
  font-weight: bold;
}

.info-msg {
  color: #059;
  background-color: #bef;
}
.success-msg {
  color: #270;
  background-color: #dff2bf;
}
.form2 {
  display: flex;
  flex-direction: column;
}
.form3 {
  display: flex;
  justify-content: center;
}
/*Loading*/
section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  align-items: center;
}
.content h2 {
  color: #fff;
  font-size: 4rem;
  position: absolute;
  transform: translate(-50%, -50%);
}
.content h2:nth-child(1) {
  color: transparent;
  -webkit-text-stroke: 2px #fff;
}
.content h2:nth-child(2) {
  color: #fff;
  animation: animate 4s ease-in-out infinite;
}
@keyframes animate {
  0%,
  100% {
    clip-path: polygon(
      0% 45%,
      16% 44%,
      33% 50%,
      54% 60%,
      70% 61%,
      84% 59%,
      100% 52%,
      100% 100%,
      0% 100%
    );
  }
  50% {
    clip-path: polygon(
      0% 60%,
      15% 65%,
      34% 66%,
      51% 62%,
      67% 50%,
      84% 45%,
      100% 46%,
      100% 100%,
      0% 100%
    );
  }
}
.langue img {
  width: 25px;
  vertical-align: bottom;
  padding-left: 15px;
}
.langue::before {
  background: transparent !important;
}
.Navbar {
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 01% 6% 1% 6%;
}
.Navbar > .nav-logo a {
  font-family: Archivo, sans-serif;
  color: #fff;
  letter-spacing: 0;
  font-size: 32px;
  font-weight: 400;
  text-decoration: none;
}
.Navbar > .nav-items > a {
  letter-spacing: 2px;
  text-transform: uppercase;
  margin-left: 35px;
  font-family: TekoRegular, sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-decoration: none;
  display: inline-block;
  color: #a0a0a0;
  position: relative;
}
.Navbar > .nav-items > a:hover {
  color: #fff;
}
.Navbar > .nav-items > a::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 2px;
  background: #ffffff;
  transition: all 0.45s;
}
.Navbar > .nav-items > a:hover::before {
  width: 100%;
}
.Navbar > .nav-toggle {
  display: none;
}
/*hero*/
.hero {
  margin: 0% 12% 0px 12%;
  height: 90vh;
  background-image: url("../../Pictures/IMG_6026.webp");
  background-size: auto 90%; /* Auto width and 100% height */

  background-repeat: no-repeat;
  background-position: 100% 40%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  animation: fadeIn ease 5s;
  -webkit-animation: fadeIn ease 5s;
  -moz-animation: fadeIn ease 5s;
  -o-animation: fadeIn ease 5s;
  -ms-animation: fadeIn ease 5s;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.Herotitle {
  color: #fff;
  letter-spacing: 0;
  font-family: Archivo, sans-serif;
  font-size: 2.6rem;
  line-height: 2.6rem;
  width: 52%;
}
.Herodescription {
  border-top: 2px solid white;
  width: 40%;
  color: #868686;
  padding-top: 35px;
  font-family: Nunito, sans-serif;
  font-size: 1.2rem;
}
.Herosocialmedia a img {
  width: 30px;
  margin-right: 15px;
}
.Herosocialmedia a img:hover {
  width: 32px;
  margin-right: 17px;
}
/*Brands*/
.Brands {
  margin: 0% 12% 0px 12%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.Brands h4 {
  margin-top: 83px;
  font-size: 1.2rem;
  letter-spacing: 0;
  font-family: Archivo, sans-serif;
  text-align: center;
  background: linear-gradient(
    to right,
    hsl(0, 0%, 30%) 0%,
    hsl(0, 0%, 100%) 10%,
    hsl(0, 0%, 30%) 20%
  );
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  animation: shine 5s infinite linear;
}
@keyframes shine {
  0% {
    background-position: 0;
  }
  60% {
    background-position: 600px;
  }
  100% {
    background-position: 600px;
  }
}
.brandslogos {
  gap: 5vw;
  display: flex;
  align-items: center;
}
.brandslogos img:nth-child(-n + 2) {
  height: 50px;
}
.brandslogos img:nth-last-child(-n + 2) {
  height: 70px;
}
.brandslogos {
  margin-bottom: 83px;
  display: flex;
  justify-content: space-around;
}
.ligne {
  width: 0.5%;
}
/*Works*/
.Works {
  padding: 2% 6% 2% 6%;
  margin: 1% 1% 1% 1%;
  height: 55vh;
  background-position: right;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  background-attachment: scroll;
}
.Works img {
  width: 10%;
}
.Works h6 {
  opacity: 0.4;
  font-size: 1rem;
  color: #fff;
  letter-spacing: 3px;
  text-transform: uppercase;
  font-family: TekoRegular, sans-serif;
  margin-bottom: 0px;
}
.Works h1 {
  width: 40%;
  color: #fff;
  text-transform: uppercase;
  font-family: TekoBold, sans-serif;
  font-size: 2rem;
  margin-bottom: 20px;
  margin-top: 30px;
  line-height: 1rem;
}
.Works h4 {
  color: #fff;
  width: 40%;
  font-family: Nunito;
}
.Send {
  padding: 1em 4em !important;
}
.Works button,
.Send {
  color: #fff;
  font-weight: bold;
  font-family: Nunito;
  text-transform: uppercase;
  background-color: transparent;
  border: 2px solid white;
  padding: 14px 24px;
}
.Works button:hover,
.Send:hover {
  color: #403f3f;
  background-color: #fff;
  cursor: pointer;
}
/*certification*/
.certificate img {
  width: 5vw;
}
.certificate {
  margin: 1% 1% 1% 1%;
  display: flex;
  justify-content: center;
  position: relative;
  flex-direction: column;
  align-items: center;
}
.youhere {
  background-color: #5a0906;
  width: 100%;
  height: 35vh;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}
.tech {
  margin-top: -5%;
  background-color: #ffffff;
  border-radius: 6px;
  box-shadow: 0 5px 5px 0 rgba(233, 240, 243, 0.5), 0 0 0 1px #e6ecf8;
  color: #141c3a;
  width: 75%;
  text-align: center;
  display: flex;
}
.tech h2 {
  color: #c23b41;
  font-size: 1.5rem;
  letter-spacing: 2px;
  font-family: Archivo, sans-serif;
}
.tech h3 {
  color: #181818;
  font-size: 1rem;
  line-height: 1.5rem;
  font-family: Nunito, sans-serif;
}
.tech h4 {
  color: #c23b41;
  letter-spacing: 0px;
  font-family: Archivo, sans-serif;
  margin-top: 50px;
  margin-bottom: 8px;
  font-size: 1.2rem;
  line-height: 1.5rem;
}
.tech div {
  color: #181818;
  border-bottom: 0px solid #e6ecf8;
  border-right: 1px solid #e6ecf8;
  padding: 60px 35px;
  flex: 1;
}
.tech .tools {
  display: flex;
  flex-direction: column;
}
.youhere h1 {
  position: relative;
  color: #fff;
  font-weight: bold;
  font-family: TekoBold, sans-serif;
  letter-spacing: 5px;
  overflow: hidden;
  border-right: 2px solid hsl(0, 0%, 80%);
  white-space: nowrap;
  animation: typewriter 4s steps(44) 1s infinite both,
    blinkTextCursor 400ms infinite;
  text-transform: uppercase;
  font-size: 2rem;
  margin-top: 3vh;
}
@keyframes typewriter {
  from {
    width: 0;
  }
  to {
    width: 500px;
  }
}
@keyframes blinkTextCursor {
  from {
    border-right-color: hsl(0, 0%, 80%);
  }
  to {
    border-right-color: transparent;
  }
}
.youhere h4 {
  font-size: 1rem;
  color: #fff;
  font-family: Nunito;
  width: 40%;
}
/*Footer*/
.footersection {
  background-color: #5a0906;
  justify-content: space-between;

  display: flex;
  flex-direction: column;
  padding: 2% 6% 2% 6%;
  margin: 1% 1% 0% 1%;
  justify-content: end;
}
.footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.footer-section1 {
  flex: 4;
  color: #fff;
  display: flex;
  gap: 5vw;
}
.footer-section2 {
  flex: 1;
  color: #fff;
}
.footer-section1 .work h1 {
  text-transform: uppercase;
  font-family: Archivo, sans-serif;
  font-size: 1.5rem;
}
.footer-section2 img {
  width: 30px;
  margin-right: 15px;
}
.footer-section1 .work h4 a {
  font-size: 1.1rem;
  font-family: Nunito;
  text-decoration: none;
  color: #fff;
}
.footer-section2 h1 {
  text-transform: uppercase;
  font-family: Archivo, sans-serif;
  font-size: 1.5rem;
  letter-spacing: 1px;
}
.footer-section1 {
  display: flex;
  flex-direction: row;
}
.copyright {
  color: #fff;
  font-family: Nunito, sans-serif;
  font-size: 0.8rem;
  padding-top: 1%;
  border-top: 1px solid white;
  letter-spacing: 2px;
}
.connect {
  display: flex;
  justify-content: center;
  margin-top: 2%;
  margin-bottom: 4%;
}
.connectbtn {
  align-items: center;
  background-color: #fcfcfd;
  border-radius: 4px;
  border-width: 0;
  box-shadow: rgba(45, 35, 66, 0.4) 0 2px 4px,
    rgba(45, 35, 66, 0.3) 0 7px 13px -3px, #d6d6e7 0 -3px 0 inset;
  box-sizing: border-box;
  color: #36395a;
  cursor: pointer;
  font-family: Nunito, sans-serif;
  height: 48px;
  padding-left: 16px;
  padding-right: 16px;
  transition: box-shadow 0.15s, transform 0.15s;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow, transform;
  font-size: 1.5rem;
}
.connectbtn:focus {
  box-shadow: #d6d6e7 0 0 0 1.5px inset, rgba(45, 35, 66, 0.4) 0 2px 4px,
    rgba(45, 35, 66, 0.3) 0 7px 13px -3px, #d6d6e7 0 -3px 0 inset;
}
.connectbtn:hover {
  box-shadow: rgba(45, 35, 66, 0.4) 0 4px 8px,
    rgba(45, 35, 66, 0.3) 0 7px 13px -3px, #d6d6e7 0 -3px 0 inset;
  transform: translateY(-2px);
}
.connectbtn:active {
  box-shadow: #d6d6e7 0 3px 7px inset;
  transform: translateY(2px);
}
/*responive*/
