    margin-bottom: 5%;
  }
  .Herodescription {
    width: 80%;
    font-size: 1.4rem;
    line-height: 1.4rem;
    font-weight: bold;
  }
  .Works img {
    width: 12%;
  }
  .Works h1 {
    width: 60%;
  }
  .Works h4 {
    width: 60%;
  }
  .Works {
    padding: 2%;
    margin-bottom: 2%;
  }
  .tech {
    width: 92%;
  }
  .youhere {
    height: 30vh;
  }
  .tech img {
    width: 30%;
  }
  .brandslogos {
    gap: 2vw;
    flex-wrap: wrap;
  }
  .footersection {
    padding: 2% 2% 2% 2%;
  }
}
@media screen and (max-width: 768px) {
  .langue img {
    width: 25px;
    padding-left: 5px;
  }
  .footer-section1 .work h1,
  .footer-section2 h1 {
    font-size: 1.2rem;
  }
  .footer {
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }
  .footer-section1 {
    display: flex;
    justify-content: center;
    text-align: center;
    gap: 25%;
  }
  .copyright {
    text-align: center;
    margin-top: 2%;
    padding-top: 15px;
  }
  /*Header*/
  .Navbar > .nav-items {
    z-index: 1;
    position: absolute;
    top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #181818;
    left: 0;
    width: 100%;
    height: 100%;
    transform: translateX(-100%);
    transition: all 0.45s;
  }
  .Navbar > .nav-items a {
    background: transparent;
    font-size: 24px;
    letter-spacing: 4px;
    margin: 0;
  }
  .Navbar > .nav-items > a::before {
    background: transparent;
    font-size: 50px;
  }
  .Navbar > .nav-items.open {
    transform: translateX(0);
  }
  .Navbar > .nav-toggle {
    display: flex;
    width: 50px;
    height: 50px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  .nav-toggle > .bar {
    position: relative;
    width: 32px;
    height: 2px;
    background: #ffffff;
    transition: all 0.45s ease-in-out;
  }
  .nav-toggle > .bar::before,
  .nav-toggle > .bar::after {
    content: "";
    position: absolute;
    height: 2px;
    background: #ffffff;
    border-radius: 2px;
    transition: all 0.45s ease-in-out;
  }
  .nav-toggle > .bar::before {
    width: 25px;
    transform: translateY(-8px);
    right: 0;
  }
  .nav-toggle > .bar::after {
    width: 32px;
    transform: translateY(8px);
  }
  .nav-toggle.open > .bar {
    transform: translateX(-40px);
    background: transparent;
  }
  .nav-toggle.open > .bar::before {
    width: 32px;
    transform: rotate(45deg) translate(26px, -26px);
  }
  .nav-toggle.open > .bar::after {
    transform: rotate(-45deg) translate(26px, 26px);
  }
  /*hero*/
  .Herotitle {
    font-size: 1.8rem;
    line-height: 2rem;
    width: 100%;
    margin-bottom: 0px;
  }
  .Herodescription {
    width: 90%;
    font-size: 1.2rem;
  }
  .ligne {
    width: 1%;
  }
  .Works img {
    width: 25%;
  }
  .tech {
    height: min-content;
    background-color: transparent;
    border: none;
    display: flex;
    flex-direction: column;
    width: 90%;
  }
  .tech img {
    width: 15%;
  }
  .youhere h1,
  .ContactForm h3 {
    font-size: 1.5rem;
  }
  .tech div {
    color: #181818;
    border-bottom: 0px solid #e6ecf8;
    border-right: 1px solid #e6ecf8;
    padding: 60px 35px;
    background-color: #ffffff;
    box-shadow: 0 5px 5px 0 rgba(233, 240, 243, 0.5), 0 0 0 1px #e6ecf8;
  }
  .brandslogos {
    flex-direction: column;
  }
  .Works {
    height: 65vh;
  }
  .brandslogos img:nth-child(-n + 2) {
    height: 30px;
  }
  .brandslogos img:nth-last-child(-n + 2) {
    height: 50px;
  }

  .hero {
    background-size: auto 100%;
    background-position: center;
  }
}
@media screen and (max-width: 480px) {
  .footer {
    padding: 3% 0% 3% 0%;
  }
  .Hero {
    height: 20vh;
  }
  @keyframes typewriter {
    from {
      width: 0;
    }
    to {
      width: 320px;
    }
  }
  .Herotitle {
    font-size: 1.5rem;
    line-height: 1.5rem;
    width: 100%;
  }
  .Herodescription {
    width: 100%;
    line-height: 1em;
  }
  .brandslogos {
    gap: 5vw;
    flex-direction: column;
  }
  .ligne {
    width: 1%;
  }
  .Works img {
    width: 30%;
  }
  .Works h1 {
    width: 100%;
  }
  .Works h4 {
    width: 90%;
  }
  .tech img {
    width: 25%;
  }
  .youhere h4 {
    width: 90%;
  }
  .youhere h1,
  .ContactForm h3 {
    font-size: 1.3rem;
    letter-spacing: 3px;
  }
  .content h2 {
    font-size: 3rem;
  }
  /*contact form*/
  .ContactForm h3 {
    width: 95%;
    font-size: 1rem;
    text-align: center;
  }
  .ContactForm h4 {
    width: 90%;
  }
  .form1 {
    flex-direction: column;
  }
  form {
    width: 90%;
  }
  .footer-section1 .work h1,
  .footer-section2 h1 {
    font-size: 1rem;
  }
  .Send {
    margin-bottom: 10%;
  }
}
